import React, { Component } from "react";
import "./App.css";

import Board from "./board";

import ContainerView from "./go";

var board = new Board(19);

const IPFS = require('ipfs')
const OrbitDB = require('orbit-db')

// OrbitDB uses Pubsub which is an experimental feature
// and need to be turned on manually.
// Note that these options need to be passed to IPFS in
// all examples even if not specified so.
const ipfsOptions = {
  EXPERIMENTAL: {
    pubsub: true
  },
  config: {
    Addresses: {
      Swarm: [
        // Use IPFS dev signal server
        // Prefer websocket over webrtc
        //
        // Websocket:
        // '/dns4/ws-star-signal-2.servep2p.com/tcp/443//wss/p2p-websocket-star',
        '/dns4/wss0.bootstrap.libp2p.io/tcp/443/wss/ipfs/QmZMxNdpMkewiVZLMRxaNxUeZpDUb34pWjZ1kZvsd16Zic',
        // Local signal server
        //'/ip4/127.0.0.1/tcp/4711/ws/p2p-websocket-star'
        //
        // WebRTC:
        // '/dns4/star-signal.cloud.ipfs.team/wss/p2p-webrtc-star',
        // Local signal server
        // '/ip4/127.0.0.1/tcp/1337/ws/p2p-webrtc-star'
      ]
    }
  }
};

// Create IPFS instance
const ipfs = new IPFS(ipfsOptions)

ipfs.on('error', (e) => console.error(e))
ipfs.on('ready', async () => {
  const orbitdb = new OrbitDB(ipfs)

  // Create / Open a database
  const db = await orbitdb.log('hello')
  await db.load()

  // Listen for updates from peers
  db.events.on('replicated', (address) => {
    console.log(db.iterator({ limit: -1 }).collect())
  })

  // Add an entry
  const hash = await db.add('world')
  console.log(hash)

  // Query
  const result = db.iterator({ limit: -1 }).collect()
  console.log(JSON.stringify(result, null, 2))
})


class App extends Component {
  render() {
    return (
      <div className="App">
        <ContainerView board={board} />
      </div>
    );
  }
}

export default App;
